@import url(https://fonts.googleapis.com/css2?family=Fugaz+One&family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
.activeLink{
	color:#000;
	position:relative;
	text-decoration:none;
	font-weight:bold;
	transition: 1s;
}

.activeLink::before{
	content: '';
	width:100%;
	position: absolute;
	bottom:-1px;
	height: 2px;
	background: red;
	left:0;
}


.myBrand{
	font-size:2em;
}

.home{min-height:90vh;display:flex;align-items:center;justify-content:center;flex-direction:column;margin-top:10vw}.home .row{min-height:80vh}.home h1{font-family:"Fugaz One",cursive;color:#35f;line-height:35px;padding:2px 3px}.home p{font-family:"Oswald",sans-serif;padding:8px 5px}.home .square_btn{display:inline-block;padding:10px 40px;border-radius:25px;text-decoration:none;color:#FFF;font-size:1.1rem;background-image:linear-gradient(45deg, #22C1f7 0%, #228bf2 57%, #1025f2 100%);transition:1s ease-in-out}.home .square_btn:hover{background-image:linear-gradient(45deg, #1025f2 0%, #228bf2 57%, #22C1f7 100%);transition:1s ease-in-out}.home ::selection{color:blue;text-decoration:underline}.home img{position:relative;width:100%}.about h1{font-family:"Fugaz One",cursive;color:#35f;font-size:2rem;margin:25px 2px}.about div p{font-size:1.3rem;margin:10px 2px;line-height:25px;font-family:"Oswald",sans-serif}.about div ol{margin:20px 3px;padding:10px 25px}.about div ol li{margin-bottom:30px}.about div ol li h3{font-size:1.25rem;font-family:"Roboto",sans-serif;color:#113355}.about div ol li p{font-size:1.1rem;color:#004466}.about ::selection{color:red}

